body {
  margin: 0;
  padding: 0;
  background: white;
}
.container {
  position: relative;
  padding-top: 5%;
  margin: auto;
  height: 80px;
  background-color: #f0860d;
}
.Hotbg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #6e289c;
  height: 60px;
  border-radius: 40px;
  padding: 10px;
}

.Hotbg:hover > .Hotbg-txt {
  width: 240px;
  padding: 0 6px;
}

.Hotbg:hover > .Hotbg-btn {
  background: azure;
  color: black;
}

.Hotbg-btn {
  color: #e84118;
  float: right;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #2f3640;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.4s;
  color: azure;
  cursor: pointer;
}

.Hotbg-btn > i {
  font-size-adjust: 30px;
}

a {
  text-decoration: none;
}

.Hotbg-txt {
  border: none;
  background: none;
  outline: none;
  float: left;
  padding: 0;
  color: azure;
  font-size: 16px;
  transition: 0.4s;
  line-height: 40px;
  width: 0px;
  font-weight: bold;
}
